body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.banner {
    color: #31b572;
    text-align: right;
    padding: 32px;
    border-bottom: solid 3px #31b572;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    align-items: center;
}

.banner__title {
    padding-left: 32px;
}

.banner__logo {
    height: 45px;
    width: 45px;
}

@media screen and (max-width: 640px) {
    .banner {
        margin: 0;
        padding: 0;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        margin-top: 30px;
    }
}
.plant__item__cover {
	height: 250px;
	width: 250px;
	border-radius: 20px;
	object-fit: cover;
}

.plant__item {
	margin: 10px;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-direction: column;
	text-transform: capitalize;
	position: relative;
}

.plant__item__price {
	position: absolute;
	border-radius: 20px 20px 20px 0;
	background-color: #31b472;
	color: white;
	padding: 15px;
	right: 0;
	top: 0;
	font-weight: 500;
}

@media screen and (max-width: 640px) {
	.plant__item__cover {
		width: 150px;
		height: 200px;
	}

	.plant__item__price {
		padding: 10px;
	}

	.plant__item {
		margin-bottom: 30px;
	}
}
.cart {
	color: white;
	background-color: #31b572;
	padding: 32px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 300px;
}

.cart__addButton {
	margin-left: 32px;
	cursor: pointer;
}

.cart__toggleButton {
	background: none;
	outline: none;
	border: none;
	font-weight: 600;
	cursor: pointer;
	align-self: self-start;
	padding: 0;
	color: white;
	align-self: flex-end;
}

.cart__closed {
	padding: 32px;
	width: 125px;
	background-color: #31b572;
}

.cart__closed > .cart__toggleButton {
	color: white;
}

.cart__flex__group {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-right: 10px;
	padding-left: 10px;
	padding-bottom: 10px;
	border-bottom: 1px solid white;
}

.cart__title {
	text-align: left;
	font-size: 17px;
}

.cart__total {
	text-align: right;
}


.cart__title__name {
	font-weight: 500;
	text-transform: capitalize;
}

.cart__flex {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin:  0;
}

.cart__flex > p {
	padding: 5px 5px 5px 20px;
	margin: 0;
}

.cart__amount {
	border-bottom: .5px dashed white;
}

.empty__button {
	background: none;
	color: white;
	font-size: 16px;
	border: 2px solid white;
	padding: 10px;
	border-radius: 20px;
	cursor: pointer;
	float: right;
}

.empty__button:hover {
	background-color: #47cc89;
}

@media screen and (max-width: 640px) {
	.cart {
		width: auto;
	}

	.cart__closed {
		width: auto;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.footer {
	color: white;
	padding: 32px;
	border-top: solid 3px white;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	align-items: center;
	font-weight: 500;
	background-color: #31b572;
}

.footer__element {
    margin-bottom: 10px;
}
.shopping__list {
	width: 100%;
}

.plant__list {
	list-style-type: none;
	padding: 32px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.addToCart__btn {
	margin-right: 20px;
	height: 20px;
	position: absolute;
	bottom: 25px;
	right: 0;
	width: 100px;
	background: none;
	background-color: #31b572;
	color: white;
	border: 2px solid #31b572;
	border-radius: 25px;
	padding-bottom: 25px;
	padding-top: 10px;
}

.addToCart__btn:hover {
	background-color: #4ccc8c;
	cursor: pointer;
}

.div__flex {
	display: flex;
	position: relative;
}

@media screen and (max-width: 640px) {
	.addToCart__btn {
		width: 70px;
		bottom: 20px;
	}

	.plant__list {
		padding: 10px;
		justify-content: space-around;
	}
}
.categories {
    text-align: center;
    padding-top: 30px;
}

.categories__select {
    margin-right: 15px;
    text-align: center;
    background: none;
    padding: 5px;
    border: 2px solid #31b572;
    border-radius: 20px;
}

.reset__button {
    background: none;
    padding: 5px;
    border: 2px solid #31b572;
    background-color: #31b572;
    color: white;
    border-radius: 25px;
}

.reset__button:hover {
    background-color: #77e2ac;
    cursor: pointer;
}
.layoutInner {
	display: flex;
	flex-direction: row;
}

@media screen and (max-width: 640px) {
	.layoutInner {
		flex-direction: column;
	}
}
